body {
  font-family: $content-font;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.link,
a {
  text-decoration: none;
}

.list {
  list-style: none;
  text-decoration: none;
}

img {
  display: block;
}

button {
  cursor: pointer;
  border: none;
  background: none;
}

a {
  list-style: none;
  text-decoration: none;
}

li {
  list-style-type: none;
}
